import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';

@Injectable()
export class MovieService {

  constructor(private http: HttpClient) { }

  getMovies() {
    return this.http.get("http://192.168.1.147:3000/api/movies/").map(function(result) {
      let data: Movie[] = [];
      for (let i in result) {
        data.push(new Movie(result[i]));
      }
      return data;
    });
  }

  getMovieDetail(id: string) {
    return this.http.get("http://192.168.1.147:3000/api/movies/"+id).map(function(result) {
      return new Movie(result);
    });
  }

}


export class Movie {
	id: number = undefined
	name: string = ""
	description: string = ""
	imgPath: string = "http://via.placeholder.com/230x350"
	duration: number = 0
	genre: any = undefined
	language: string = "English"
	mpaaRating: any = {}
	userRating: string = undefined

  constructor(json?: any) {
    if (json) {
      for (var key in json) {
        if (key in this && json[key]) {
          this[key] = JSON.parse(JSON.stringify(json[key]));
        }
      }
    }
  }
}


